// footer styles
.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 50px 100px;
  @include r(1280) {
    padding: 20px; }
  @include r(767) {
    flex-wrap: wrap; }
  @include r(420) {
    flex-direction: column; }
  .socials {
    @include r(767) {
      order: 1;
      width: 100%;
      text-align: center;
      margin-bottom: 20px; }
    @include r(420) {
      margin-bottom: 10px; } } }

.copyright {
  color: #ccc;
  @include r(767) {
    order: 2;
    letter-spacing: 0; }
  @include r(420) {
    margin-bottom: 10px; } }

.createby {
  color: #D1D1D1;
  @include r(767) {
    order: 3;
    letter-spacing: 0; }
  a {
    color: $gray;
    text-transform: uppercase;
    &:hover {
      color: $brown; } } }
